.subscribebox{
    width: 100%;
    height: 70vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    position: relative;
    background: url("../../assets/images/subscribe/traveller.jpg");
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;

    .heading{
        color: white;
        font-size: 50px;
        padding: 2rem 0;
        font-weight: 500;
    }

    .email-form{
        display: flex;
        align-items: center;

        .email-input input{
            padding: 20px;
            width: 30vw;
            border-top-left-radius: 50px;
            border-bottom-left-radius: 50px;
            border: none;
        }

        .submit button{
            padding: 1rem;
            border-top-right-radius: 50%;
            border-bottom-right-radius: 50%;
            background: cadetblue;
            border: none;
            font-size: 19.5px;
            cursor: pointer;
            color: white;
            font-weight: 600;
        }
    }

}