.hotelbox{
    position: relative;

    .overlay{
        position: absolute;
        width: 100%;
        height: 70vh;
        background-color: rgba(0, 0, 0, 0.356);
    }

    img{
        width: 100%;
        min-height: 70vh;
        height: fit-content;
        object-fit: cover;
    }

    .book-btn{
        position: absolute;
        top: 10vh;
        right: 0;
        rotate: 90deg;
        width: 100px;
        height: 4vh;
        background-color: transparent;
        border: 0.5px solid white;
        color: white;
        font-weight: 400;
        font-size: 15px;
        cursor: pointer;
        letter-spacing: 0.1rem;
        z-index: 2;

        &:hover{
            background-color: white;
            color: black;
        }
    }

    .hoteldiv{
        position: absolute;
        bottom: 5vh;
        color: white;
        text-transform: uppercase;
        width: 100%;

        .rate{
            font-size: 20px;
            margin: 10px;
            z-index: 2;

        }

        .name{
            font-size: 30px;
            margin: 10px;
            z-index: 2;

        }

        .line{
            width: 4vw;
            left: 0;
            margin-left: 10px;
            transition: width 1s ease;
            z-index: 2;
        }

        .hover-div{
            display: none;
        }

    }
    
    &:hover{
        .line{
            width: 90%;
        }

        .hover-div{
            display: flex;
            justify-content: space-between;
            width: 100%;
            align-items: center;

            .icons-div{
                display: flex;
                gap: 10px;
                font-size: 20px;
                font-weight: 600;
                margin: 10px;
                z-index: 2;
            }

            .details{
                margin: 10px;
                z-index: 2;
            }

            a{
                width: 100px;
                height: 4vh;
                background-color: transparent;
                border: 0.5px solid white;
                color: white;
                font-weight: 400;
                font-size: 15px;
                cursor: pointer;
                letter-spacing: 0.1rem;
                text-decoration: none;
                display: flex;
                justify-content: center;
                align-items: center;
                z-index: 2;            
                &:hover{
                    background-color: white;
                    color: black;
                }
            }

        }
    }
}

