@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;600&family=Great+Vibes&family=Oswald:wght@200;400&display=swap');

.roomsbox{
    background-color: #F8F5F0;
    padding: 10vh 8vw;

    .hotel-name{
        font-family: 'Oswald', sans-serif;
        font-weight: 200;
        letter-spacing: 0.1rem;
        font-size: 20px;
        color: gray;
        margin: 3vh 0 0 1.5vw;

    }

    .room-title{
        font-weight: 500;
        font-size: 45px;
        margin: 3vh 0 0 1vw;
        padding: 0;
        color: navy;
        letter-spacing: 0.1rem;
        font-family: 'Castoro Titling', cursive;
    }

    .row{
        flex: 1;
        display: flex;
        justify-content: center;
        gap: 10px;
        margin: 0 10px;

        .col{
            flex: 1;
            height: 70vh;
            margin: 10px;
        }

        .col-l{
            height: 50vh;
            margin: 10px;
        }
    }
}