.countitembox{
    position: relative;

    img{
        width: 100%;
        height: 100%;
        border-radius: 15px;
    }

    .overlay{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 99%;
        background-color: rgba(0, 0, 0, 0.279);
        border-radius: 15px;

    }

    .value{
        position: absolute;
        bottom: 16vh;
        left: 3vw;
        font-size: 5rem;
        color: white;
        font-weight: 500;
    }

    .name{
        position: absolute;
        bottom: 8vh;
        left: 3vw;
        font-size: 3.5rem;
        color: white;
        font-weight: 500;
    }

}