@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Oswald:wght@200;400&family=Wix+Madefor+Display&display=swap');

.footerbox{
    display: flex;
    justify-content: space-around;
    padding: 10vh 7vw;
    gap: 4vw;
    background-color: #F8F5F0;

    .grid-div{
        flex: 1;

        .header{
            font-weight: 500;
            color: navy;
            font-size: 25px;
            text-transform: uppercase;
            line-height: 20px;
            padding-bottom: 10px;
            font-family: 'Oswald', sans-serif;
            letter-spacing: 0.05rem;
            height: 6vh;

            .tagline{
                font-weight: 400;
                font-size: 12px;
                color: gray;
                text-transform:lowercase;
                font-family: 'Montserrat', sans-serif;
                letter-spacing: normal;

            }
        }

        .info{
            font-family: 'Wix Madefor Display', sans-serif;
            font-size: 16px;
            line-height: 2rem;
            color: rgb(50, 50, 50);
            font-weight: 400;
        }

        .offer{
            font-family: 'Wix Madefor Display', sans-serif;
            line-height: 2rem;
            text-decoration: none;

            .offer-title{
                font-size: 18px;
                color: rgb(10, 8, 8);
                font-weight: 400;


                &:hover{
                    color: rgb(39, 50, 167);
                }
            }

            .offer-info{
                font-size: 15px;
                margin-bottom: 20px;
                color: gray;
                font-weight: 400;
            }
            
        }

        .footer-link{
            font-family: 'Wix Madefor Display', sans-serif;
            line-height: 2rem;
            text-decoration: none;
            display: flex;
            align-items: center;
            gap: 5px;

            .point{
                transform: rotate(90deg);
            }

            .hotel{
                font-size: 15px;
                color: rgb(10, 8, 8);
                font-weight: 400;
                margin-bottom: 5px;
                
                &:hover{
                    color: rgb(39, 50, 167);
                }
            }
        }

        .contact{
            font-family: 'Wix Madefor Display', sans-serif;
            line-height: 2rem;
            text-decoration: none;
            display: flex;
            align-items: center;
            gap: 15px;

            .point-info{
                font-size: 18px;
                color: rgb(10, 8, 8);
                font-weight: 400;
                margin-bottom: 5px;
            }
        }
    }
}