.title{
    font-size: 40px;
    font-weight: 400;
    margin: 20vh 10vw 5vh 15vw;
    text-transform: uppercase;
    color: navy;
    letter-spacing: 0.1rem;
    font-family: 'Castoro Titling', cursive;
}

.horizontalaccordianbox{
    display: flex;
    justify-content: center;
    padding: 0 10vw 15vh 10vw;

    .info-div{
        flex: 2;
        border-bottom: 1px solid black;
        display: flex;
        justify-content: center;
        align-items: center;
        transform: skewX(10deg);
        transform-origin: top left;
        background-color: #ffffff;
        border-top: 1px solid slategray;
        z-index: 2;
        padding: 5vh 2vw 5vh 5vw;
        -webkit-box-shadow: 27px 29px 22px -22px rgb(255, 255, 255);
        -moz-box-shadow: 27px 29px 22px -22px rgb(255, 255, 255);
        box-shadow: 27px 29px 22px -22px rgb(237,205,148);
    }

    .accordian-div{
        flex: 0.5;
        -webkit-box-shadow: 26px -27px 0px -16px rgb(255, 255, 255);
        -moz-box-shadow: 26px -27px 0px -16px rgb(255, 255, 255);
        box-shadow: 26px -27px 0px -16px rgb(237,205,148);

        .year{
            border-bottom: 1px solid black;
            color: rgb(25, 12, 96);
            font-weight: 600;
            cursor: pointer;
            text-align: end;
            padding: 4vh 1.2vw 4vh 5vw;
            background: rgb(129,229,230);
            background: linear-gradient(90deg, rgba(129,229,230,1) 0%, rgba(255,255,255,1) 100%);

            &:last-child{
                padding: 8vh 1.2vw 21.5vh 4vw;
                border-bottom: 1px solid black;
            }

        
            &:hover{
                color: black;
                background: rgb(255, 255, 255);
                background: linear-gradient(90deg, rgb(255, 255, 255) 29%, rgb(255, 255, 255) 100%);
            }

        }  

    }
}