.fixedbgbox{
    width: 100%;
    height: 110vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url("../../assets/images/fixedbg/sunset-pool.jpg");
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
   
    position: relative;

    .overlay{
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.271);
    }
    
    .text{
        z-index: 2;
        color: white;
        font-size: 70px;
        font-weight: 600;
        padding: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        text-align: center;
    }
}

