    .carouselbox{
        width: 100%;
        position: relative;
        height: calc(100Vh - 5px);
        overflow: hidden;
    
        .carousel{
            width: 500vw;
            height: 100%;
            display: flex;
            transition: all 1s;

            img{
                width: 100vw;
                height: 100%;
                object-fit: cover;
            }

        }

        .buttons{
            position: absolute;
            display: flex;
            justify-content: center;
            gap: 20px;
            bottom: 50px;
            left: 0;
            right: 0;
            margin: auto;
            align-items: center;

            .button{
                height: 55px;
                width: 55px;
                border: 1px solid white;
                display: flex;
                justify-content: center;
                align-items: center;
                color: white;
                font-size: 20px;
                cursor: pointer;

                &:hover{
                    background-color: #ffffff;
                    color: blue;
                }
            }
        }

    }
