@import url('https://fonts.googleapis.com/css2?family=Castoro+Titling&family=Dancing+Script&display=swap');

.titlebox{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .welcome{
        font-size: 13px;
        text-transform: uppercase;
        color: navy;
        margin: 10vh 10vw 1vh 10vw;
        letter-spacing: 0.1rem;
        font-weight: 400;
        font-family:Verdana, Geneva, Tahoma, sans-serif;
    }

    .title{
        font-size: 60px;
        font-weight: 500;
        margin: 1vh 10vw 0 10vw;
        text-transform: uppercase;
        color: navy;
        letter-spacing: 0.2rem;
        font-family: 'Castoro Titling', cursive;
    }

    .tagline{
        font-size: 15px;
        text-transform: uppercase;
        color: navy;
        letter-spacing: 0.1rem;
        font-weight: 600;
        letter-spacing: 0.2rem;
        font-family: 'Dancing Script', cursive;
    }

}