@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;600&family=Great+Vibes&family=Oswald:wght@400;600&display=swap');

.navbar{
    display: flex;
    justify-content: center;
    align-items: center;
    height: fit-content;
    padding-top: 5px;
    position: absolute;
    width: 100%;
    background-color: transparent;
    z-index: 2;

    .navbar-container{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        max-width: 1280px;

        .logo{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            background-color: #ffff;
            text-decoration: none;
            padding: 10px 0;
            
            img{
                width: fit-content;
                height: fit-content;
            }
            
            .logo-title{
                font-size: 30px;
                font-family: 'Oswald', sans-serif;
                font-weight: 600;
                color: gray;
                text-transform: uppercase;
            }

            .logo-tagline{
                font-size: 20px;
                font-weight: 400;
                font-family: 'Great Vibes', cursive;
                color: gray;
            }

        }
        
        .navbar-items{
            display: flex;
            justify-content: space-around;
            align-items: center;
            list-style: none;
            gap: 40px;
            padding: 2rem;
            text-transform: capitalize;

            .nav-item{
                color: white;
                font-size: 20px;
                cursor: pointer;
            }
        }
    }
}