.scrolltotopbox{
    position: fixed;
    right: 2vw;
    bottom: 5vh;
    z-index: 99;
    background-color: navy;
    width: 45px;
    height: 45px;
    padding: 10px;
    border-radius: 50%;
    color: white;
    cursor: pointer;
    
    &.hidden{
        display: none;
    }
}
