@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Oswald:wght@200;400&display=swap');


.testimonialsbox{
    position: relative;
    width: 100%;
    height: 60vh;

    
    .title{
        margin: 15vh 0 5vh 0;
        padding: 0;
        text-align: center;
    }
    
    .arrows{
        display: flex;
        justify-content: center;
        gap: 40vw;
        align-items: center;
        font-size: 25px;
        
        .next-comment{
            cursor: pointer;
            color: gray;
            font-weight: 400;
        }
        .prev-comment{
            cursor: pointer;
            font-weight: 400;
        }
    }

    .testimonials-div{
        padding: 20vh 0;

        .testimony{
            position: absolute;
            top: 20vh;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            text-align: center;
            width: 35vw;
            z-index: 0;
            opacity: 0;
            transition: 1s;

            .author{
                font-size: 25px;
                font-family: 'Oswald', sans-serif;
            }

            .comment{
                font-size: 16px;
                font-weight: 400;
                font-family: 'Montserrat', sans-serif;
                padding: 1rem 1vw;
            }

            .stars{
                color: gold;
            }
            
            &.testimony--active {
                opacity: 1;
                transform: scale(1.1);
                z-index: 99;       
            }
          
            &.testimony--left {
                transform: translateX(-90%) scale(0.5);
                transition: 1s;
                opacity: 0.3;
            }
        
            &.testimony--right {
                transform: translateX(90%) scale(0.5);
                transition: 1s;
                opacity: 0.3;                
            }
        }

    }

}