@import url('https://fonts.googleapis.com/css2?family=Archivo+Narrow&family=Great+Vibes&family=Lobster&family=Oswald:wght@300;400&display=swap');

.accordianinfobox{
    width: 100%;
    height: 100%;

    .top-para{
        display: flex;
        gap: 20px;
        justify-content: center;
        align-items: center;

        .img-div{
            flex: 1;
            height: 300px;
            transform: skewX(-10deg);
            transform-origin: top left;

            img{
                width: 100%;
                height: 100%;
            }
        }   
        
        .info{
            flex: 1;
            height: 300px;
            transform: skewX(-10deg);
            transform-origin: top left;
            font-size: 16px;
            line-height: 1.85;
            font-family: 'Oswald', sans-serif;
            color: rgb(83, 81, 81);
        }
    }

    .bottom-para{
        margin: 2vh 0 0 0;

        .bottom-info{
            flex: 2;
            height: 100%;
            transform: skewX(-10deg); 
            transform-origin: top left;
            margin-left: -3vw;
            font-size: 16px;
            line-height: 1.85;
            font-family: 'Oswald', sans-serif;
            color: rgb(83, 81, 81);
        }

    }

}