.swiperbox{
    height: 110vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #ECFAFB;

    .title{
        text-align: center;
        margin: 0;
        padding: 0;
        font-size: 45px;
    }

    .tagline{
        font-weight: 400;
        text-align: center;
    }

    .owlcarouselbox{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 30px;
        position: relative;

        .img-div{
            position: relative;
            
            .overlay{
                position: absolute;
                top: 0;
                left: 0;
                width: 300px;
                height: 300px;
                background-color: #c7b3827f;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 30px;
                color: white;
                cursor: pointer;
                display: none;
            }

            img{
                width: 300px;
                height: 300px;
            }

            .info-div{
                width: 300px;
                text-align: center;
                background-color: #ffffff;

                .heading{
                    font-weight: 400;
                    padding: 6px 0;
                    text-transform: uppercase;
                    color: slategray;
                }

                .info{
                    font-weight: 400;
                    padding: 0.5rem 1rem;
                    color: gray;
                }
            }

            &:hover{

                .overlay{
                    display: flex;
                }
            }
        }
        
        .swiper{
            position: relative;
            padding: 10vh 0;
            
        }
        .swiper-button-next{
            position: absolute;
            top: 22px;
            right: 3rem;
            font-size: 10px;
            border: 1px solid black;
            background-color: white;
            width: 60px;
            height: 60px;
        }
        .swiper-button-prev{
            position: absolute;
            top: 22px;
            right: 8rem;
            left: auto;
            font-size: 10px;
            border: 1px solid black;
            background-color: white;
            width: 60px;
            height: 60px;
        }
        
        :root{
            --swiper-theme-color: navy;
        }
    }
    

} 